
.navbar{
	width:100%;
	height:49px;
	position: fixed;
	left:0;
	bottom:0;
	z-index:10;
	display: flex;
	background: #f8f8f8;
}
.navbar:before{
	content:'';
	display: block;
	height:1px;
	width:100%;
	border-top:1px solid #CCCCCC;
	position:absolute;
	top:-1px;
	left:0;
	transform: scaleY(0.5);
}
.navbar a{
	flex:1;
	text-align: center;
	color: #666;
	position: relative;
}
.navbar a:last-child .weui-badge{
	display: none;
}
.navbar .iconfont{
	display: block;
	width: 100%;
	text-align: center;
	color: #666;
	margin-top:4px;
}
.navbar a.router-link-active .iconfont{
	color: #10AEFF;
}
.navbar a.router-link-active p{
	color: #10AEFF;
}
.page{
	width:100%;
	position: absolute;
	left:0;
	top:0;
	bottom:49px;
	height: auto;
}
.slideInRight{
	animation-duration: 600ms;
}
.slideOutRight{
	animation-duration: 600ms;
}
.weui-search-bar:before{
		border-top:none;
}
.list{	
	width:100%;
	z-index: 100;
}
.list li{
	border-bottom:1px solid #e5e5e5;
	padding:10px 15px;
	color:lightslategrey
}
li.no_task{
	text-align:center; 
	border:none;
	margin-top:30px;
}
li>p{
	width:100%;
	height: 22px;
	line-height: 22px;
	margin-bottom:4px;
}
p .task_name{
	display:block;
	width:200px;
	overflow: hidden;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-size:16px;
	color: #666666;	
	margin-bottom: 3px;
	font-weight: 700;
	float:left;
}
p .do_user{
	font-size:14px;
	float:left;
}
p .status{
	line-height: 22px;
	padding: 0 2px;
	display: inline-block;
	text-align: center;
	background-color:#10AEFF;
	color:#fff;
	float:right;
}
.status.near{
	background-color:#FFBE00 ;
}
.status.over{
	background-color:#F76260;
}
.code{
	display: inline-block;
}
.date{
	float: right;
	text-align: center;
	color:#666;
}
.bottom{
	width:100%;
	height:30px;
	line-height: 30px;
	text-align: center;
}

































































































#finish .status{
	background-color:#09BB07 ;
}
#finish .wrapper{
	margin-bottom: 15px;
}


#td_detail{
	width:100%;
	height: 100%;
	position:absolute;
	left:0;
	top:0;
	z-index:100;	
	background: #fff;
}
#mescroll .wrapper{
	padding-bottom: 60px;
	background: #fff;
}
.vux-no-group-title,.vux-no-group-title{
	margin-top:0 !important;
}
.weui-cell:before{
	left:0 !important;
}
.vux-cell-primary {
text-align: left !important;
}
.vux-cell-box{
	color:#999;
}
.weui-dialog__bd:first-child {
	padding:0px !important;
}
.weui-wepay-flow, .weui-wepay-flow-auto{
	padding: 0 !important;
	position: absolute;
	left:-20px;
	top:15px;
	height:100%;
}
.weui-wepay-flow__li .weui-wepay-flow__state {
    width: 20px !important;
    height: 20px !important;
	border-radius: 50% !important;;
	line-height: 20px !important;
}
.btn{
	position: fixed;
	left: 0;
	bottom:0;
	width:100%;
	text-align: center;
	display: flex;
	height:46px;
	line-height: 46px;
	color:#fff;
	font-size:16px;
	z-index:10;
}
.btn span{
	flex:1;
}
.btn span:first-child{
	background-color: #09bb07;
}
.btn span:last-child{
	background-color: #E64340;
}
.process{
	width:100%;
	background: #F0F2F5;
}
.process .agree_status{	
	background: #FFF;
	padding-bottom:10px;
}
.agree_status .allInfo{
	margin-left:30px;
	position: relative;
}
.agree_status .info{
	border-bottom:1px solid #ccc;
	padding: 10px;
	position: relative;
	vertical-align: middle;
}
.allInfo:last-child{
	margin-bottom:10px;
}
.allInfo:last-child .arrow{
	display: none;
}
#td_detail .allInfo:last-child .weui-wepay-flow__line{
	display: none;
}
.info p{
	display: block;
	margin-left:30px;
	font-size:15px;
}
.info p span{
	display: inline-block;
	margin-bottom:5px;
	margin-right: 20px;
}
.qjDetail{
	padding: 15px;
}
.qjDetail:before {
    content: " ";
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    height: 1px;
    border-top: 1px solid #D9D9D9;
    /* color: #D9D9D9;
    transform-origin: 0 0; */
    transform: scaleY(0.5);
}
.qjDetail ul{
	border:1px solid #ccc;
	padding:10px;
}
.qjDetail ul:last-child{
	position: relative;
}
.qjDetail ul li{
	line-height: 25px;
}
.qjDetail ul li span{
	margin-right: 10px;
}
.qgProcess:last-child .arrow{
	display: none;
}
.choice{
	width:100%;
	margin-top:45px;
	background: #fff;
}
.choice P{
	font-size:15px;
}
.choice ul li{
	border-bottom: 1px solid #d9d9d9;
	position: relative;
}
.choice ul li p.title{
	position:absolute;
	left:0;
	top:-32px;
	width:100%;
	border-bottom: 1px solid #d9d9d9;
	font-size:15px;
	padding-left: 20px;
	background:#fff;
	display:none;
	margin-bottom:0;
	height:32px;
	line-height:32px;
}
.choice ul li:first-child .title{
	display:block;
}
.choice ul li .iconfont{
	position: absolute;
	left:20px;
	top:15px;
}
ul li .choice_content{
	padding-left: 50px;
}
.choice .choice_content>p{
	width:100%;
	line-height: 30px;
	padding-right:10px;
}
.choice_content p em{
	float:right;
}

